  transform: translate(100%);
  height: 100vh;
  width: 100%;
  max-width: 448px;
  padding: 0 20px 0 40px;
  background-color: #017ee2;
  transition: transform 0.3s ease;
}
.main-menu_open {
  transform: translate(0);
}
@media (min-width: 640px) {
  .main-menu {
    padding: 0 60px;
  }
}

.main-menu__header {
  display: flex;
  justify-content: flex-end;
