.menu-button_close {
  cursor: pointer;
  border: none;
  background: none;
  padding: 2px 1px;
  font-size: 11px;
  font-family: "RobotoCondensed", sans-serif;
  font-weight: 900;
  line-height: 13px;
  text-transform: uppercase;
  color: #fff;
  border-top: 3px solid #fff;
  border-bottom: 3px solid #fff;
  transition: color 0.3s ease, border-color 0.3s ease;
